.btn,
.jumbotron,
.nav :hover {
  border-radius: 0px;
}

.card-cus {
  border-radius: 10px;
  background: #ffffff;
  box-shadow: 5px 5px 10px #bebebe, -5px -5px 10px #ffffff;
}

@media only screen and (max-width: 768px) {
  /* For mobile phones: */
  #addProjectButton {
    position: fixed;
    right: 20px;
    bottom: 20px;
    height: 70px;
    width: 70px;
    z-index: 999999;
  }
  #plusIcon{
    font-size: 1.7em;
  }
}

@media only screen and (min-width: 769px) {
  /* For mobile phones: */
  #addProjectButton {
    margin-bottom: 20px;
  }
}

body {
  background: #f2f2f5;
}
.text-strike{
  text-decoration: line-through;
}
.custom-check{
  height: 18px;
  width: 18px;
}
.hidden{
  display: none;
}

